import React from 'react';
import T from 'prop-types';
import { withTheme } from 'styled-components';
import { PureSingleValue, config } from '@splunk/visualizations/SingleValue';
import { withSanitizedProps } from '@splunk/visualizations-shared/SanitizeProps';
import { propsConvert, propsDeprecate } from '../../utils/propUtils';

// This propsMapping is for React SingleValue to use PureSingleValue.
const propsMapping = {
    value: 'majorValue',
    deltaValue: 'trendValue',
    deltaColor: 'trendColor',
    deltaFontSize: 'trendFontSize',
    graphData: 'sparklineValues',
    useTrendUnits: 'shouldAbbreviateTrendValue',
    useThousandSeparators: 'shouldUseThousandSeparators',
    sparklineAcceptNullData: 'shouldSparklineAcceptNullData',
    sparklineAreaGraph: 'showSparklineAreaGraph',
    sparklineFillColor: 'sparklineAreaColor',
};

const deprecatedProps = ['showTrendIndicator', 'trendDisplayMode', 'showSparkline', 'sparklinePosition'];

const ReactSingleValue = ({ ...props }) => {
    let newProps = propsConvert(props, propsMapping);
    // change props
    const trendDisplay = newProps.showTrendIndicator ? newProps.trendDisplayMode : 'off';
    const sparklineDisplay = newProps.showSparkline ? newProps.sparklinePosition : 'off';
    const defaultColors = {
        defaultFontColor: config.themes.defaultFontColor(props),
        defaultBackgroundColor: config.themes.defaultBackgroundColor(props),
        defaultBlockFontColor: config.themes.defaultBlockFontColor(props),
        sparklineSeverityColors: JSON.parse(config.themes.sparklineSeverityColors(props)),
    };
    const sparklineAreaColor = newProps.sparklineAreaColor || config.themes.sparklineFillColor(props);

    newProps = {
        ...propsDeprecate(newProps, deprecatedProps),
        ...defaultColors,
        sparklineAreaColor,
        trendDisplay,
        sparklineDisplay,
    };
    return <PureSingleValue {...newProps} />;
};

ReactSingleValue.propTypes = {
    width: T.oneOfType([T.string, T.number]),
    height: T.oneOfType([T.string, T.number]),
    style: T.object,
    graphData: T.arrayOf(T.oneOfType([T.string, T.number])),
    value: T.oneOfType([T.string, T.number]),
    majorColor: T.string,
    majorFontSize: T.number,
    deltaValue: T.oneOfType([T.string, T.number]),
    deltaColor: T.string,
    deltaFontSize: T.number,
    trendDisplayMode: T.string,
    useTrendUnits: T.bool,
    showTrendIndicator: T.bool,
    backgroundColor: T.string,
    unit: T.string,
    unitPosition: T.string,
    underLabel: T.string,
    underLabelFontSize: T.number,
    numberPrecision: T.number,
    useThousandSeparators: T.bool,
    showSparkline: T.bool,
    showSparklineTooltip: T.bool,
    sparklineAreaGraph: T.bool,
    sparklinePosition: T.string,
    sparklineFillColor: T.string,
    sparklineStrokeColor: T.string,
    sparklineAcceptNullData: T.bool,
    sparklineHighlightSegments: T.number,
    sparklineHighlightDots: T.number,
    onValueClick: T.func,
};

ReactSingleValue.defaultProps = {
    width: '100%',
    height: 250,
    trendDisplayMode: 'absolute',
    useTrendUnits: false,
    showTrendIndicator: true,
    unitPosition: 'after',
    underLabelFontSize: 12,
    numberPrecision: 0,
    useThousandSeparators: true,
    showSparkline: true,
    showSparklineTooltip: false,
    sparklineAreaGraph: false,
    sparklinePosition: 'below',
    sparklineAcceptNullData: true,
    sparklineHighlightSegments: 0,
    sparklineHighlightDots: 0,
    onValueClick: () => {},
};

export default withTheme(withSanitizedProps(ReactSingleValue));
